module.exports = "/*----------contact tabs----*/\r\n/* Style the tab content */\r\nul.tab {\r\n  list-style-type: none;\r\n  margin: 0;\r\n  padding: 0;\r\n  overflow: hidden;\r\n  background-color: #f1f1f1;\r\n}\r\n/* Float the list items side by side */\r\nul.tab li {\r\n    float: left;\r\n  }\r\n/* Style the links inside the list items */\r\nul.tab li button {\r\n      margin-left:0px;\r\n      display: inline-block;\r\n      color: #0c5030;\r\n      text-align: center;\r\n      padding: 14px 16px;\r\n      text-decoration: none;\r\n      transition: 0.3s;\r\n      font-size: 19px;\r\n      border: 1px solid #ccc;\r\n    }\r\n/* Change background color of links on hover */\r\nul.tab li button:hover {\r\n        background-color: #ddd;\r\n      }\r\n/* Create an active/current tablink class */\r\nul.tab li button:focus, .active {\r\n        background-color: white;\r\n        border-bottom: none !important;\r\n      }\r\n.tabcontent {\r\n  display: none;\r\n  padding: 6px 12px;\r\n  border-top: none;\r\n  margin: 30px;\r\n}\r\n.contactdetailstabs {\r\n  width: 90%;\r\n  border: 1px solid #ccc;\r\n  height: auto;\r\n}\r\n@-webkit-keyframes fadeEffect {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n@keyframes fadeEffect {\r\n  from {\r\n    opacity: 0;\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n  }\r\n}\r\n/*---end tabs--*/\r\n/*---style card*/\r\n/* entire container, keeps perspective */\r\n.card-container {\r\n  -webkit-perspective: 600px;\r\n  -o-perspective: 600px;\r\n  perspective: 600px;\r\n  margin-bottom: 30px;\r\n  margin-left: -250px;\r\n}\r\n/* flip the pane when hovered */\r\n.card-container:hover .card, .card-container.hover .card {\r\n    -webkit-transform: rotateY( 0deg );\r\n    transform: rotateY( 0deg );\r\n  }\r\n.card-container.static:hover .card, .card-container.static.hover .card {\r\n    -webkit-transform: none;\r\n    transform: none;\r\n  }\r\n/* flip speed goes here */\r\n.card {\r\n  transition: -webkit-transform .5s;\r\n  transition: transform .5s;\r\n  transition: transform .5s, -webkit-transform .5s;\r\n  -webkit-transform-style: preserve-3d;\r\n  transform-style: preserve-3d;\r\n  position: relative;\r\n}\r\n/* hide back of pane during swap */\r\n.front, .back {\r\n  -webkit-backface-visibility: hidden;\r\n  backface-visibility: hidden;\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  background-color: #FFF;\r\n  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.14);\r\n}\r\n/* front pane, placed above back */\r\n.front {\r\n  z-index: 2;\r\n}\r\n/* back, initially hidden pane */\r\n.back {\r\n  -webkit-transform: rotateY( 0deg );\r\n  transform: rotateY( 0deg );\r\n  z-index: 3;\r\n}\r\n.card {\r\n  background: none repeat scroll 0 0 #FFFFFF;\r\n  border-radius: 4px;\r\n  color: #444444;\r\n}\r\n.card-container, .front, .back {\r\n  width: 100%;\r\n  height: 320px;\r\n  border-radius: 4px;\r\n}\r\n.card .cover {\r\n  height: 105px;\r\n  overflow: hidden;\r\n  border-radius: 4px 4px 0 0;\r\n}\r\n.card .cover img {\r\n    width: 100%;\r\n  }\r\n.card .user {\r\n  border-radius: 50%;\r\n  display: block;\r\n  height: 120px;\r\n  margin: -55px auto 0;\r\n  overflow: hidden;\r\n  width: 120px;\r\n}\r\n.card .user img {\r\n    background: none repeat scroll 0 0 #FFFFFF;\r\n    width: 100%;\r\n    height: 100%;\r\n  }\r\n.card .content {\r\n  background-color: rgba(0, 0, 0, 0);\r\n  box-shadow: none;\r\n  padding: 10px 20px 20px;\r\n}\r\n.card .content .main {\r\n    min-height: 160px;\r\n  }\r\n.card .back .content .main {\r\n  height: 215px;\r\n}\r\n.card .name {\r\n  font-size: 22px;\r\n  line-height: 28px;\r\n  margin: 10px 0 0;\r\n  text-align: center;\r\n  text-transform: capitalize;\r\n}\r\n.card h5 {\r\n  margin: 5px 0;\r\n  font-weight: 400;\r\n  line-height: 20px;\r\n}\r\n.card .profession {\r\n  color: #999999;\r\n  text-align: center;\r\n  margin-bottom: 20px;\r\n}\r\n.card .footer {\r\n  border-top: 1px solid #EEEEEE;\r\n  color: #999999;\r\n  margin: 30px 0 0;\r\n  padding: 10px 0 0;\r\n  text-align: center;\r\n}\r\n.card .footer .social-links {\r\n    font-size: 18px;\r\n  }\r\n.card .footer .social-links a {\r\n      margin: 0 7px;\r\n    }\r\n.card .header {\r\n  padding: 15px 20px;\r\n  height: 90px;\r\n}\r\n.card .motto {\r\n  border-bottom: 1px solid #EEEEEE;\r\n  color: #999999;\r\n  font-size: 14px;\r\n  font-weight: 400;\r\n  padding-bottom: 10px;\r\n  text-align: center;\r\n}\r\n/*      Just for presentation        */\r\n.title {\r\n  color: #506A85;\r\n  text-align: center;\r\n  font-weight: 300;\r\n  font-size: 44px;\r\n  margin-bottom: 90px;\r\n  line-height: 90%;\r\n}\r\n.title small {\r\n    font-size: 17px;\r\n    color: #999;\r\n    text-transform: uppercase;\r\n    margin: 0;\r\n  }\r\n.space-50 {\r\n  height: 50px;\r\n  display: block;\r\n}\r\n.space-200 {\r\n  height: 200px;\r\n  display: block;\r\n}\r\n.white-board {\r\n  background-color: #FFFFFF;\r\n  min-height: 200px;\r\n  padding: 60px 60px 20px;\r\n}\r\n.ct-heart {\r\n  color: #F74933;\r\n}\r\npre.prettyprint {\r\n  background-color: #ffffff;\r\n  border: 1px solid #999;\r\n  margin-top: 20px;\r\n  padding: 20px;\r\n  text-align: left;\r\n}\r\n.atv, .str {\r\n  color: #05AE0E;\r\n}\r\n.tag, .pln, .kwd {\r\n  color: #3472F7;\r\n}\r\n.atn {\r\n  color: #2C93FF;\r\n}\r\n.pln {\r\n  color: #333;\r\n}\r\n.com {\r\n  color: #999;\r\n}\r\n/*---end style card*/\r\n.viewrow {\r\n  list-style-type: none;\r\n  border-bottom: 1px solid #e0e0e0;\r\n  padding: 2em 0;\r\n}\r\n#container {\r\n  width: 100%;\r\n  text-align: center;\r\n}\r\n#left {\r\n  float: left;\r\n  width: 30%;\r\n  text-align: left;\r\n  font-size: 18px;\r\n}\r\n#center {\r\n  display: inline-block;\r\n  margin: 0 auto;\r\n  width: 100px;\r\n}\r\n#right {\r\n  width: 40%;\r\n}\r\n/*--loader*/\r\n.loader-hidden {\r\n  display: none;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"